import React from 'react';

export default () => (
  <form role="form">
    {/* Should this really be an h2? */}
    {/* not sure about the BEM approach to ARIA - i.e. c-form__title for ID */}
    <h2 className="c-form__title">Form title</h2>
    <div className="o-layout">
      <input className="o-layout__item c-form__input" type="text" placeholder="First name" aria-label="First name"/>
      <input className="o-layout__item c-form__input" type="text" placeholder="Last name" aria-label="Last name"/>
      <input className="o-layout__item c-form__input" type="text" placeholder="Email address" aria-label="Email address"/>
      <select className="o-layout__item c-form__input c-form__select" name="city" aria-label="City" defaultValue="">
        <option value="" disabled>City</option>
        <option value="kingston">Kingston</option>
        <option value="montego-bay">Montego Bay</option>
        <option value="ocho-rios">Ocho Rios</option>
      </select>
    </div>
    <button className="c-form__submit">Submit</button>
  </form>
);



